/* eslint-disable react/jsx-props-no-spreading */
import { useState } from 'react';
// eslint-disable-next-line import/no-unresolved
// eslint-disable-next-line import/no-extraneous-dependencies
import { useForm } from 'react-hook-form';
import emailjs from '@emailjs/browser';
import CelebrationButton from '../../CelebrationButton/elebrationButton';
import './ContactForm.scss';

function ContactForm() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');

  const {
    register,
    handleSubmit,
    formState: { errors, isValid },
  } = useForm({ mode: 'onSubmit' });

  const onSubmit = (data, e) => {
    e.preventDefault();
    emailjs
      .sendForm(
        process.env.REACT_APP_SERVICE_ID,
        process.env.REACT_APP_TEMPLATE_ID,
        e.target,
        process.env.REACT_APP_PUBLIC_KEY
      )
      .then(
        (response) => {
          console.log('SUCCESS!', response.status, response.text);
          setName('');
          setEmail('');
          setMessage('');
          //        
        },
        (error) => {
          console.log('FAILED...', error);
          //  -     
        }
      );
  };

  //     ,        
  const handleConfetti = () => (!isValid ? 'invalid' : null);

  return (
    <form className="contact-form" onSubmit={handleSubmit(onSubmit)} noValidate>
      <div className="contact-form__box">
        <label htmlFor="name-input" className="contact-form__name-input">
          <input
            {...register('name_from', {
              required: '   ',
              minLength: {
                value: 2,
                message: '    ',
              },
              maxLength: {
                value: 30,
                message: '   30 ',
              },
              pattern: {
                value: /^[-A-Z[\]h-]+$/imu,
                message:
                  '  , ,   ',
              },
              onChange: (e) => setName(e.target.value),
            })}
            className="contact-form__input contact-form__name"
            placeholder="Your name"
            type="text"
            name="name_from"
            id="name-input"
            value={name}
            autoComplete="off"
          />
          {errors?.name_from && (
            <span className="contact-form__error">
              {errors?.name_from?.message || 'something wrong...'}
            </span>
          )}
        </label>
        <label htmlFor="email-input" className="contact-form__email-input">
          <input
            {...register('email_from', {
              required: '   ',
              minLength: {
                value: 2,
                message: '    ',
              },
              maxLength: {
                value: 50,
                message: '   50 ',
              },
              pattern: {
                value:
                  /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/iu,
                message: '   ',
              },
              onChange: (e) => setEmail(e.target.value),
            })}
            className="contact-form__input contact-form__email"
            placeholder="E-mail"
            type="email"
            name="email_from"
            id="email-input"
            value={email}
            autoComplete="off"
          />
          {errors?.email_from && (
            <span className="contact-form__error">
              {errors?.email_from?.message || 'something wrong...'}
            </span>
          )}
        </label>
      </div>
      <div className="contact-form__box">
        <label htmlFor="message-input" className="contact-form__message-input">
          <textarea
            {...register('message_from', {
              required: '   ',
              minLength: {
                value: 10,
                message: '   10 ',
              },
              onChange: (e) => setMessage(e.target.value),
            })}
            className="contact-form__input contact-form__text"
            placeholder="Leave your message"
            name="message_from"
            id="message-input"
            value={message}
            autoComplete="off"
          />
          {errors?.message_from && (
            <span className="contact-form__error">
              {errors?.message_from?.message || 'something wrong...'}
            </span>
          )}
        </label>
        <CelebrationButton
          isDisabled={false}
          buttonText="SEND"
          coordinateX={0.75}
          coordinateY={0.7}
          handleCelebrationButton={() => handleConfetti()}
        />
      </div>
    </form>
  );
}

export default ContactForm;
